'use client'

import { useState } from 'react'
import Link from 'next/link'

// Types
type Ticket = {
  id: string
  subject: string
  description: string
  status: 'Open' | 'In Progress' | 'Resolved' | 'Closed'
  priority: 'Low' | 'Medium' | 'High' | 'Urgent'
  category: string
  companyId: string
  companyName: string
  subAccountId?: string
  subAccountName?: string
  submitter: {
    name: string
    email: string
  }
  assignedTo?: {
    name: string
    email: string
  }
  createdAt: string
  updatedAt: string
  lastReply?: string
}

// Mock Data
const mockTickets: Ticket[] = [
  // Open Tickets
  { id: 'tick-001', subject: 'Unable to access dashboard', description: 'Getting 404 error when trying to access the main dashboard', status: 'Open', priority: 'High', category: 'Technical', companyId: 'comp-001', companyName: 'Tech Solutions Inc.', subAccountId: 'sub-001', subAccountName: 'Sales Team', submitter: { name: 'Mike Davis', email: 'mike@techsolutions.com' }, createdAt: '2024-12-08 09:15', updatedAt: '2024-12-08 09:15' },
  { id: 'tick-002', subject: 'Payment method update needed', description: 'Need to update credit card information for billing', status: 'Open', priority: 'Medium', category: 'Billing', companyId: 'comp-002', companyName: 'Digital Marketing Pro', submitter: { name: 'Sarah Johnson', email: 'sarah@digitalmarketing.com' }, createdAt: '2024-12-08 08:30', updatedAt: '2024-12-08 08:30' },
  { id: 'tick-003', subject: 'Feature request: Custom reports', description: 'Would like to have custom report builder functionality', status: 'Open', priority: 'Low', category: 'Feature Request', companyId: 'comp-003', companyName: 'Creative Agency Co.', submitter: { name: 'Michael Chen', email: 'michael@creativeagency.com' }, createdAt: '2024-12-08 07:45', updatedAt: '2024-12-08 07:45' },
  { id: 'tick-004', subject: 'User permissions not working', description: 'Sub-account users cannot access certain features they should have access to', status: 'Open', priority: 'Urgent', category: 'Technical', companyId: 'comp-004', companyName: 'Global Enterprises', subAccountId: 'sub-015', subAccountName: 'North America', submitter: { name: 'Patricia Davis', email: 'patricia@globalenterprises.com' }, assignedTo: { name: 'Admin Team', email: 'admin@risivo.com' }, createdAt: '2024-12-08 06:20', updatedAt: '2024-12-08 10:15', lastReply: '2024-12-08 10:15' },
  
  // In Progress Tickets
  { id: 'tick-005', subject: 'Slow loading times', description: 'Dashboard takes 10+ seconds to load', status: 'In Progress', priority: 'High', category: 'Performance', companyId: 'comp-005', companyName: 'StartupHub Accelerator', submitter: { name: 'David Park', email: 'david@startuphub.com' }, assignedTo: { name: 'Tech Support', email: 'tech@risivo.com' }, createdAt: '2024-12-07 16:30', updatedAt: '2024-12-08 11:20', lastReply: '2024-12-08 11:20' },
  { id: 'tick-006', subject: 'Data export issue', description: 'Cannot export user data to CSV format', status: 'In Progress', priority: 'Medium', category: 'Technical', companyId: 'comp-006', companyName: 'HealthTech Innovations', submitter: { name: 'Dr. Lisa Wong', email: 'lisa@healthtech.com' }, assignedTo: { name: 'Development Team', email: 'dev@risivo.com' }, createdAt: '2024-12-07 14:15', updatedAt: '2024-12-08 09:45', lastReply: '2024-12-08 09:45' },
  { id: 'tick-007', subject: 'Integration with third-party API', description: 'Need help setting up API integration with Salesforce', status: 'In Progress', priority: 'Medium', category: 'Integration', companyId: 'comp-007', companyName: 'EduLearn Platform', submitter: { name: 'Prof. Robert Taylor', email: 'robert@edulearn.com' }, assignedTo: { name: 'Integration Team', email: 'integrations@risivo.com' }, createdAt: '2024-12-07 11:50', updatedAt: '2024-12-08 08:30', lastReply: '2024-12-08 08:30' },
  
  // Resolved Tickets
  { id: 'tick-008', subject: 'Password reset not working', description: 'Password reset emails are not being received', status: 'Resolved', priority: 'High', category: 'Account', companyId: 'comp-008', companyName: 'FinServe Solutions', submitter: { name: 'James Martinez', email: 'james@finserve.com' }, assignedTo: { name: 'Support Team', email: 'support@risivo.com' }, createdAt: '2024-12-06 15:20', updatedAt: '2024-12-07 10:30', lastReply: '2024-12-07 10:30' },
  { id: 'tick-009', subject: 'Invoice discrepancy', description: 'Last month invoice shows incorrect amount', status: 'Resolved', priority: 'High', category: 'Billing', companyId: 'comp-009', companyName: 'RetailConnect Corp', submitter: { name: 'Amanda Wilson', email: 'amanda@retailconnect.com' }, assignedTo: { name: 'Billing Team', email: 'billing@risivo.com' }, createdAt: '2024-12-06 13:45', updatedAt: '2024-12-07 09:15', lastReply: '2024-12-07 09:15' },
  { id: 'tick-010', subject: 'Email notifications not arriving', description: 'Not receiving any email notifications from the platform', status: 'Resolved', priority: 'Medium', category: 'Technical', companyId: 'comp-010', companyName: 'GreenEnergy Systems', submitter: { name: 'Carlos Rodriguez', email: 'carlos@greenenergy.com' }, assignedTo: { name: 'Tech Support', email: 'tech@risivo.com' }, createdAt: '2024-12-06 10:30', updatedAt: '2024-12-07 08:45', lastReply: '2024-12-07 08:45' },
  
  // Closed Tickets
  { id: 'tick-011', subject: 'Training session request', description: 'Would like to schedule training for new team members', status: 'Closed', priority: 'Low', category: 'Training', companyId: 'comp-001', companyName: 'Tech Solutions Inc.', submitter: { name: 'John Smith', email: 'john@techsolutions.com' }, assignedTo: { name: 'Training Team', email: 'training@risivo.com' }, createdAt: '2024-12-05 14:20', updatedAt: '2024-12-06 16:30', lastReply: '2024-12-06 16:30' },
  { id: 'tick-012', subject: 'General inquiry about features', description: 'Questions about upcoming features and roadmap', status: 'Closed', priority: 'Low', category: 'General', companyId: 'comp-002', companyName: 'Digital Marketing Pro', submitter: { name: 'Sarah Johnson', email: 'sarah@digitalmarketing.com' }, assignedTo: { name: 'Customer Success', email: 'success@risivo.com' }, createdAt: '2024-12-05 11:15', updatedAt: '2024-12-06 14:20', lastReply: '2024-12-06 14:20' },
]

export default function PlatformSupportTicketsPage() {
  const [tickets] = useState<Ticket[]>(mockTickets)
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [priorityFilter, setPriorityFilter] = useState<string>('all')
  const [categoryFilter, setCategoryFilter] = useState<string>('all')
  const [companyFilter, setCompanyFilter] = useState<string>('all')
  const [sortBy, setSortBy] = useState<'created' | 'updated' | 'priority'>('created')
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc')
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedTickets, setSelectedTickets] = useState<string[]>([])
  const itemsPerPage = 10

  // Get unique values for filters
  const companies = Array.from(new Set(tickets.map(t => t.companyName))).sort()
  const categories = Array.from(new Set(tickets.map(t => t.category))).sort()

  // Filter tickets
  const filteredTickets = tickets.filter(ticket => {
    const matchesSearch = 
      ticket.subject.toLowerCase().includes(searchQuery.toLowerCase()) ||
      ticket.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
      ticket.companyName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      ticket.submitter.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      ticket.submitter.email.toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesStatus = statusFilter === 'all' || ticket.status === statusFilter
    const matchesPriority = priorityFilter === 'all' || ticket.priority === priorityFilter
    const matchesCategory = categoryFilter === 'all' || ticket.category === categoryFilter
    const matchesCompany = companyFilter === 'all' || ticket.companyName === companyFilter
    
    return matchesSearch && matchesStatus && matchesPriority && matchesCategory && matchesCompany
  })

  // Sort tickets
  const sortedTickets = [...filteredTickets].sort((a, b) => {
    let comparison = 0
    
    if (sortBy === 'created') {
      comparison = new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
    } else if (sortBy === 'updated') {
      comparison = new Date(a.updatedAt).getTime() - new Date(b.updatedAt).getTime()
    } else if (sortBy === 'priority') {
      const priorityOrder = { 'Urgent': 4, 'High': 3, 'Medium': 2, 'Low': 1 }
      comparison = priorityOrder[a.priority] - priorityOrder[b.priority]
    }
    
    return sortOrder === 'asc' ? comparison : -comparison
  })

  // Pagination
  const totalPages = Math.ceil(sortedTickets.length / itemsPerPage)
  const startIndex = (currentPage - 1) * itemsPerPage
  const paginatedTickets = sortedTickets.slice(startIndex, startIndex + itemsPerPage)

  // Statistics
  const stats = {
    total: tickets.length,
    open: tickets.filter(t => t.status === 'Open').length,
    inProgress: tickets.filter(t => t.status === 'In Progress').length,
    resolved: tickets.filter(t => t.status === 'Resolved').length,
    closed: tickets.filter(t => t.status === 'Closed').length,
    urgent: tickets.filter(t => t.priority === 'Urgent').length,
  }

  // Handle select/deselect
  const handleSelectAll = () => {
    if (selectedTickets.length === paginatedTickets.length) {
      setSelectedTickets([])
    } else {
      setSelectedTickets(paginatedTickets.map(t => t.id))
    }
  }

  const handleSelectTicket = (id: string) => {
    setSelectedTickets(prev =>
      prev.includes(id) ? prev.filter(tId => tId !== id) : [...prev, id]
    )
  }

  // Badge colors
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Open': return 'bg-blue-100 text-blue-800'
      case 'In Progress': return 'bg-yellow-100 text-yellow-800'
      case 'Resolved': return 'bg-green-100 text-green-800'
      case 'Closed': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'Urgent': return 'bg-red-100 text-red-800'
      case 'High': return 'bg-orange-100 text-orange-800'
      case 'Medium': return 'bg-yellow-100 text-yellow-800'
      case 'Low': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
        <div>
          <nav className="flex items-center text-sm text-gray-500 mb-2">
            <Link href="/sub-account/platform-admin" className="hover:text-gray-700">
              Platform Admin
            </Link>
            <span className="mx-2">/</span>
            <span className="text-gray-900">Support Tickets</span>
          </nav>
          <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
            <span className="text-2xl">üé´</span>
            Support Tickets
          </h1>
          <p className="text-gray-500 text-sm mt-1">
            Manage and respond to customer support requests
          </p>
        </div>
        <div className="flex items-center gap-2">
          <button className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700 flex items-center gap-2">
            <span>‚ûï</span>
            Create Ticket
          </button>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-3 md:grid-cols-6 gap-4">
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-purple-600">{stats.total}</div>
          <div className="text-sm text-gray-600">Total</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-blue-600">{stats.open}</div>
          <div className="text-sm text-gray-600">Open</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-yellow-600">{stats.inProgress}</div>
          <div className="text-sm text-gray-600">In Progress</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-green-600">{stats.resolved}</div>
          <div className="text-sm text-gray-600">Resolved</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-gray-600">{stats.closed}</div>
          <div className="text-sm text-gray-600">Closed</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-red-600">{stats.urgent}</div>
          <div className="text-sm text-gray-600">Urgent</div>
        </div>
      </div>

      {/* Bulk Actions */}
      {selectedTickets.length > 0 && (
        <div className="bg-purple-50 border border-purple-200 rounded-xl p-4 flex items-center justify-between">
          <span className="text-sm font-medium text-purple-800">
            {selectedTickets.length} ticket{selectedTickets.length > 1 ? 's' : ''} selected
          </span>
          <div className="flex items-center gap-2">
            <button className="px-3 py-1.5 bg-yellow-600 text-white rounded-lg text-sm hover:bg-yellow-700">
              Assign
            </button>
            <button className="px-3 py-1.5 bg-green-600 text-white rounded-lg text-sm hover:bg-green-700">
              Resolve
            </button>
            <button className="px-3 py-1.5 bg-gray-600 text-white rounded-lg text-sm hover:bg-gray-700">
              Close
            </button>
            <button className="px-3 py-1.5 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700">
              Export
            </button>
            <button
              onClick={() => setSelectedTickets([])}
              className="px-3 py-1.5 bg-white text-gray-700 border border-gray-300 rounded-lg text-sm hover:bg-gray-50"
            >
              Cancel
            </button>
          </div>
        </div>
      )}

      {/* Filters */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm p-4">
        <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
          {/* Search */}
          <div className="md:col-span-2">
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </div>
              <input
                type="text"
                placeholder="Search tickets, companies, submitters..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
              />
            </div>
          </div>

          {/* Status Filter */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Status</option>
            <option value="Open">Open</option>
            <option value="In Progress">In Progress</option>
            <option value="Resolved">Resolved</option>
            <option value="Closed">Closed</option>
          </select>

          {/* Priority Filter */}
          <select
            value={priorityFilter}
            onChange={(e) => setPriorityFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Priorities</option>
            <option value="Urgent">Urgent</option>
            <option value="High">High</option>
            <option value="Medium">Medium</option>
            <option value="Low">Low</option>
          </select>

          {/* Category Filter */}
          <select
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Categories</option>
            {categories.map(category => (
              <option key={category} value={category}>{category}</option>
            ))}
          </select>
        </div>

        {/* Second Row */}
        <div className="mt-4 pt-4 border-t border-gray-200 flex items-center justify-between">
          <select
            value={companyFilter}
            onChange={(e) => setCompanyFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Companies</option>
            {companies.map(company => (
              <option key={company} value={company}>{company}</option>
            ))}
          </select>
          
          <div className="flex items-center gap-2">
            <span className="text-sm text-gray-500">Sort by:</span>
            {(['created', 'updated', 'priority'] as const).map((option) => (
              <button
                key={option}
                onClick={() => {
                  if (sortBy === option) {
                    setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc')
                  } else {
                    setSortBy(option)
                    setSortOrder('desc')
                  }
                }}
                className={`px-3 py-1.5 rounded-lg text-sm ${
                  sortBy === option
                    ? 'bg-purple-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {option.charAt(0).toUpperCase() + option.slice(1)}
                {sortBy === option && (sortOrder === 'asc' ? ' ‚Üë' : ' ‚Üì')}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Tickets Table */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left">
                  <input
                    type="checkbox"
                    checked={selectedTickets.length === paginatedTickets.length && paginatedTickets.length > 0}
                    onChange={handleSelectAll}
                    className="rounded border-gray-300 text-purple-600 focus:ring-purple-500"
                  />
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Ticket
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Company
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Submitter
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Priority
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Assigned
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Created
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {paginatedTickets.map((ticket) => (
                <tr key={ticket.id} className="hover:bg-gray-50">
                  <td className="px-4 py-4">
                    <input
                      type="checkbox"
                      checked={selectedTickets.includes(ticket.id)}
                      onChange={() => handleSelectTicket(ticket.id)}
                      className="rounded border-gray-300 text-purple-600 focus:ring-purple-500"
                    />
                  </td>
                  <td className="px-4 py-4">
                    <Link href={`/sub-account/platform-admin/support/${ticket.id}`} className="hover:underline">
                      <div className="font-medium text-gray-900">{ticket.subject}</div>
                      <div className="text-sm text-gray-500">{ticket.id}</div>
                      <div className="text-xs text-purple-600">{ticket.category}</div>
                    </Link>
                  </td>
                  <td className="px-4 py-4">
                    <div className="font-medium text-gray-900">{ticket.companyName}</div>
                    {ticket.subAccountName && (
                      <div className="text-sm text-gray-500">üìç {ticket.subAccountName}</div>
                    )}
                  </td>
                  <td className="px-4 py-4">
                    <div className="font-medium text-gray-900">{ticket.submitter.name}</div>
                    <div className="text-sm text-gray-500">{ticket.submitter.email}</div>
                  </td>
                  <td className="px-4 py-4">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${getPriorityColor(ticket.priority)}`}>
                      {ticket.priority}
                    </span>
                  </td>
                  <td className="px-4 py-4">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(ticket.status)}`}>
                      {ticket.status}
                    </span>
                  </td>
                  <td className="px-4 py-4">
                    {ticket.assignedTo ? (
                      <div>
                        <div className="font-medium text-gray-900 text-sm">{ticket.assignedTo.name}</div>
                      </div>
                    ) : (
                      <span className="text-gray-400 text-sm">Unassigned</span>
                    )}
                  </td>
                  <td className="px-4 py-4">
                    <div className="text-sm text-gray-900">{ticket.createdAt}</div>
                    {ticket.lastReply && (
                      <div className="text-xs text-gray-500">Reply: {ticket.lastReply}</div>
                    )}
                  </td>
                  <td className="px-4 py-4">
                    <Link
                      href={`/sub-account/platform-admin/support/${ticket.id}`}
                      className="px-3 py-1 bg-purple-600 text-white rounded-lg text-sm hover:bg-purple-700"
                    >
                      View
                    </Link>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="bg-gray-50 px-4 py-3 border-t border-gray-200 flex items-center justify-between">
          <div className="text-sm text-gray-600">
            Showing {startIndex + 1} to {Math.min(startIndex + itemsPerPage, sortedTickets.length)} of {sortedTickets.length} tickets
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
              disabled={currentPage === 1}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Previous
            </button>
            {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
              const page = i + 1
              return (
                <button
                  key={page}
                  onClick={() => setCurrentPage(page)}
                  className={`px-3 py-1 rounded-lg text-sm ${
                    currentPage === page
                      ? 'bg-purple-600 text-white'
                      : 'border border-gray-300 hover:bg-gray-100'
                  }`}
                >
                  {page}
                </button>
              )
            })}
            <button
              onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
              disabled={currentPage === totalPages}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
